import * as React from "react"
import { Link } from "gatsby"

import Header from "../layout/Header"
import HeroSlider from "../blog/HeroSlider"
import PostCard from "../blog/PostCard"
import TrendingList from "../blog/TrendingList"
import CategorySection from "../blog/CategorySection"
import Newsletter from "../layout/Newsletter"
import Footer from "../layout/Footer"
import { slugify } from "../../utils/slugify"

const CATEGORY_LABELS = [
  {
    name: "Guest Experience",
    image: "/images/blogs/category-guest-experience/hero.jpg",
  },
  {
    name: "Host Stories",
    image: "/images/blogs/category-host-stories/hero.jpg",
  },
  {
    name: "Hosting 101",
    image: "/images/blogs/category-hosting-101/hero.jpg",
  },
  {
    name: "Hosting Tools",
    image: "/images/blogs/category-hosting-tools/hero.jpg",
  },
  {
    name: "Insights & Trends",
    image: "/images/blogs/category-insights-trends/hero.jpg",
  },
  {
    name: "Managing Property",
    image: "/images/blogs/category-managing-property/hero.jpg",
  },
  {
    name: "Pricing Strategies",
    image: "/images/blogs/category-pricing-strategies/hero.jpg",
  },
  {
    name: "Scaling Tips",
    image: "/images/blogs/category-scaling-tips/hero.jpg",
  },
]

const HosthustleHome = ({ posts = [], categoryCounts = [] }) => {
  const editorsPickPosts = posts.slice(0, 3)
  const bestPosts = posts.slice(3, 6)
  const trendingPosts = posts.slice(6, 10)
  const popularPosts = posts.slice(10, 16)

  const categories = CATEGORY_LABELS.map(({ name, image }) => {
    const normalized = name.toLowerCase()
    const slugSegment = slugify(name)

    const category = categoryCounts.find(c => c.fieldValue.toLowerCase() === normalized)
    const postCount = category ? category.totalCount : 0

    return {
      name,
      image,
      postCount,
      slug: `/blog/category/${slugSegment}/`,
    }
  })

  return (
    <div className="min-h-screen bg-white">
      <Header />

      <main>
        <HeroSlider />

        <div className="max-w-[1200px] mx-auto px-4 py-12">
          <div className="grid grid-cols-1 lg:grid-cols-12 gap-8">
            <div className="lg:col-span-4 space-y-6">
              <h2 className="text-[var(--color-editorial-black)] pb-3 border-b border-[var(--color-editorial-border)]" style={{ fontWeight: 600 }}>
                Editor&apos;s Pick
              </h2>

              {editorsPickPosts.length > 0 && (
                <Link key={editorsPickPosts[0].fields.slug} to={editorsPickPosts[0].fields.slug}>
                  <PostCard
                    variant="grid"
                    image={editorsPickPosts[0].frontmatter.cover}
                    category={editorsPickPosts[0].frontmatter.category || "LIFESTYLE"}
                    title={editorsPickPosts[0].frontmatter.title}
                    excerpt={editorsPickPosts[0].frontmatter.description || editorsPickPosts[0].excerpt}
                    author="Hosthustle"
                    date={editorsPickPosts[0].frontmatter.date}
                  />
                </Link>
              )}

              <div className="space-y-4">
                {editorsPickPosts.slice(1).map(post => (
                  <Link key={post.fields.slug} to={post.fields.slug}>
                    <PostCard
                      variant="small"
                      image={post.frontmatter.cover}
                      category={post.frontmatter.category || "FOOD"}
                      title={post.frontmatter.title}
                      author="Hosthustle"
                      date={post.frontmatter.date}
                    />
                  </Link>
                ))}
              </div>
            </div>

            <div className="lg:col-span-5 space-y-6">
              <h2 className="text-[var(--color-editorial-black)] pb-3 border-b border-[var(--color-editorial-border)]" style={{ fontWeight: 600 }}>
                Best Posts
              </h2>

              {bestPosts.length > 0 && (
                <Link key={bestPosts[0].fields.slug} to={bestPosts[0].fields.slug}>
                  <PostCard
                    variant="grid"
                    image={bestPosts[0].frontmatter.cover}
                    category={bestPosts[0].frontmatter.category || "Hosting 101"}
                    title={bestPosts[0].frontmatter.title}
                    excerpt={bestPosts[0].frontmatter.description || bestPosts[0].excerpt}
                    author="Hosthustle"
                    date={bestPosts[0].frontmatter.date}
                  />
                </Link>
              )}

              <div className="space-y-4">
                {bestPosts.slice(1).map(post => (
                  <Link key={post.fields.slug} to={post.fields.slug}>
                    <PostCard
                      variant="small"
                      image={post.frontmatter.cover}
                      category={post.frontmatter.category || "Hosting 101"}
                      title={post.frontmatter.title}
                      author="Hosthustle"
                      date={post.frontmatter.date}
                    />
                  </Link>
                ))}
              </div>
            </div>

            <div className="lg:col-span-3">
              <TrendingList items={trendingPosts} />
            </div>
          </div>
        </div>

        {/* <div className="bg-[var(--color-editorial-light-bg)] py-12">
          <div className="max-w-[1200px] mx-auto px-4">
            <h2 className="mb-8 text-[var(--color-editorial-black)] pb-3 border-b border-[var(--color-editorial-border)]" style={{ fontWeight: 600 }}>
              Popular Posts
            </h2>

            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {popularPosts.map(post => (
                <Link key={post.fields.slug} to={post.fields.slug}>
                  <PostCard
                    variant="grid"
                    image={post.frontmatter.cover}
                    category={post.frontmatter.category || "Insights & Trends"}
                    title={post.frontmatter.title}
                    excerpt={post.frontmatter.description || post.excerpt}
                    author="Hosthustle"
                    date={post.frontmatter.date}
                  />
                </Link>
              ))}
            </div>
          </div>
        </div> */}

        <CategorySection title="Browse by Category" categories={categories} />

        <Newsletter />
      </main>

      <Footer />
    </div>
  )
}

export default HosthustleHome
