import * as React from "react"
import { graphql } from "gatsby"

import Seo from "../components/seo/Seo"
import HosthustleHome from "../components/home/Home"

const IndexPage = ({ data }) => {
  const posts = data.allMdx.nodes || []
  const categoryCounts = data.categories.group || []
  return <HosthustleHome posts={posts} categoryCounts={categoryCounts} />
}

export default IndexPage

export const Head = () => <Seo title="Hosthustle" />

export const pageQuery = graphql`
  {
    allMdx(sort: { frontmatter: { date: DESC } }, limit: 20) {
      nodes {
        excerpt(pruneLength: 200)
        fields {
          slug
        }
        frontmatter {
          title
          date(formatString: "MMMM DD, YYYY")
          description
          category
          cover
          coverAlt
        }
      }
    }
    categories: allMdx {
      group(field: { frontmatter: { category: SELECT } }) {
        fieldValue
        totalCount
      }
    }
  }
`
