import * as React from "react"
import { Link } from "gatsby"

const CategoryCard = ({ name, image, postCount, slug }) => {
  return (
    <Link to={slug || "#"} className="block group cursor-pointer bg-white rounded-lg overflow-hidden editorial-shadow hover:shadow-xl transition-shadow">
      <div className="relative aspect-[4/3] overflow-hidden">
        <img
          src={image}
          alt={name}
          className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
        />
        <div className="absolute inset-0 bg-gradient-to-t from-black/60 to-transparent" />
        <div className="absolute bottom-0 left-0 right-0 p-5 text-white">
          <h3 className="mb-1" style={{ fontWeight: 600 }}>
            {name}
          </h3>
          <p style={{ fontSize: "var(--text-xs)", opacity: 0.9 }}>{postCount} Posts</p>
        </div>
      </div>
    </Link>
  )
}

const CategorySection = ({ title, categories }) => {
  return (
    <section className="py-12">
      <div className="max-w-[1200px] mx-auto px-4">
        <div className="mb-8">
          <h2
            className="text-[var(--color-editorial-black)]"
            style={{ fontWeight: 600 }}
          >
            {title}
          </h2>
        </div>

        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
          {categories.map((category, index) => (
            <CategoryCard key={index} {...category} />
          ))}
        </div>
      </div>
    </section>
  )
}

export default CategorySection
