import * as React from "react"
import { useState } from "react"
import { Link } from "gatsby"
import { ChevronLeft, ChevronRight } from "lucide-react"

const slides = [
  {
    id: 1,
    image:
      "/images/blogs/3-low-cost-instagrammable-corners-that-double-your-listings-appeal/hero.jpg",
    category: "Guest Experience",
    title: "Low-Cost 'Instagrammable' Corners That Double Your Listing's Appeal",
    excerpt: "Discover how to attract more guests by creating Instagrammable corners on a budget. Learn practical tips to enhance your space's appeal.",
    author: "HostHustle",
    date: "December 11, 2025",
    link: "/blog/3-low-cost-instagrammable-corners-that-double-your-listings-appeal",
  },
  {
    id: 2,
    image:
      "/images/blogs/5-brutal-lessons-i-learned-after-losing-฿100000-on-a-bad-thai-hosting-deal/hero.jpg",
    category: "Hosting Tools",
    title: "5 Brutal Lessons I Learned After Losing $100,000 on a Bad Thai Hosting Deal",
    excerpt:
      "Learn from a host's story of overcoming a disastrous Thai property deal. Discover strategies for resilience and the importance of community support.",
    author: "HostHustle",
    date: "December 18, 2025",
    link: "/blog/5-brutal-lessons-i-learned-after-losing-฿100000-on-a-bad-thai-hosting-deal",
  },
  {
    id: 3,
    image:
      "/images/blogs/how-one-simple-rule-can-prevent-90-percent-of-guest-problems/hero.jpg",
    category: "Hosting 101",
    title: "How One Simple Rule Can Prevent 90 Percent of Guest Problems",
    excerpt:
      "Learn how a clear, well-communicated rule can prevent most guest problems and make hosting easier.",
    author: "HostHustle",
    date: "December 17, 2025",
    link: "/blog/how-one-simple-rule-can-prevent-90-percent-of-guest-problems",
  },
]

const HeroSlider = () => {
  const [currentSlide, setCurrentSlide] = useState(0)

  const nextSlide = () => {
    setCurrentSlide(prev => (prev + 1) % slides.length)
  }

  const prevSlide = () => {
    setCurrentSlide(prev => (prev - 1 + slides.length) % slides.length)
  }

  const goToSlide = index => {
    setCurrentSlide(index)
  }

  const slide = slides[currentSlide]

  return (
    <div className="relative bg-[var(--color-editorial-light-bg)] overflow-hidden">
      <div className="max-w-[1200px] mx-auto px-4 py-12 md:py-16">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8 items-center">
          <div className="relative aspect-[4/3] overflow-hidden rounded-lg">
            <img
              src={slide.image}
              alt={slide.title}
              className="w-full h-full object-cover"
            />

            <button
              onClick={prevSlide}
              className="absolute left-4 top-1/2 -translate-y-1/2 w-10 h-10 bg-white/90 hover:bg-white rounded-full flex items-center justify-center transition-all shadow-lg"
              aria-label="Previous slide"
            >
              <ChevronLeft className="w-5 h-5 text-[var(--color-editorial-black)]" />
            </button>
            <button
              onClick={nextSlide}
              className="absolute right-4 top-1/2 -translate-y-1/2 w-10 h-10 bg-white/90 hover:bg-white rounded-full flex items-center justify-center transition-all shadow-lg"
              aria-label="Next slide"
            >
              <ChevronRight className="w-5 h-5 text-[var(--color-editorial-black)]" />
            </button>
          </div>

          <div className="bg-white p-8 md:p-10 rounded-lg editorial-shadow">
            <div className="mb-3">
              <span
                className="inline-block px-3 py-1 bg-[var(--color-editorial-accent)] text-white rounded-full"
                style={{ fontSize: "var(--text-xs)" }}
              >
                EDITOR&apos;S PICK
              </span>
            </div>

            <div className="mb-2">
              <span
                className="text-[var(--color-editorial-accent)]"
                style={{ fontSize: "var(--text-sm)" }}
              >
                {slide.category}
              </span>
            </div>

            <Link to={slide.link} className="no-underline">
              <h2
                className="mb-4 text-[var(--color-editorial-black)] hover:text-[var(--color-editorial-accent)] transition-colors"
                style={{ fontSize: "var(--text-2xl)", lineHeight: "1.3", fontWeight: 600 }}
              >
                {slide.title}
              </h2>
            </Link>

            <p
              className="mb-6 text-[var(--color-editorial-grey)]"
              style={{ lineHeight: "1.6" }}
            >
              {slide.excerpt}
            </p>

            <div className="flex items-center gap-4 mb-6 pb-6 border-b border-[var(--color-editorial-border)]">
              <span
                className="text-[var(--color-editorial-grey)]"
                style={{ fontSize: "var(--text-xs)" }}
              >
                By {slide.author}
              </span>
              <span
                className="text-[var(--color-editorial-grey)]"
                style={{ fontSize: "var(--text-xs)" }}
              >
                {slide.date}
              </span>
            </div>

            <Link 
              to={slide.link}
              className="inline-block px-6 py-3 bg-[var(--color-editorial-accent)] text-white rounded hover:bg-[var(--color-editorial-black)] transition-colors no-underline"
            >
              Continue Reading
            </Link>
          </div>
        </div>

        <div className="flex items-center justify-center gap-2 mt-8">
          {slides.map((_, index) => (
            <button
              key={index}
              onClick={() => goToSlide(index)}
              className="w-11 h-11 rounded-full flex items-center justify-center transition-all"
              aria-label={`Go to slide ${index + 1}`}
            >
              <span className={`w-2 h-2 rounded-full transition-all ${index === currentSlide
                  ? "bg-[var(--color-editorial-accent)] w-6"
                  : "bg-[var(--color-editorial-border)]"
                }`} />
            </button>
          ))}
        </div>
      </div>
    </div>
  )
}

export default HeroSlider
